}
.screenshots-intro p {
  margin-bottom: 25px;
  color: #24374b;
}

.screenshots-padding {
  margin-bottom: 50px;
}

/* ==========================================================================
Screenshots
========================================================================== */
.screenshots ul {
  margin: 0;
  padding: 0;
  width: 100%;
}
.screenshots ul li {
  float: left;
  min-height: 100%;
  width: 25%;
  background-color: #000;
  list-style: none;
}
.screenshots figure {
  position: relative;
  overflow: hidden;
}
.screenshots figure img {
  width: 100%;
  height: 100%;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}
.screenshots figure:hover img,
.screenshots figure:focus img {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}
.screenshots figcaption {
  position: absolute;
  top: 0;
  left: 0;
  padding: 25% 0;
  width: 100%;
  height: 100%;
  background-color: rgba(132, 102, 63, 0.79);
  text-align: center;
  font-size: 15px;
  opacity: 0;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}
.screenshots figcaption a {
  color: #fff;
}
.screenshots figcaption a:hover,
.screenshots figcaption a:focus {
  color: #463833;
}
.screenshots figure:hover figcaption,
.screenshots figure:focus figcaption {
  opacity: 1;
}
.visible {
  opacity: 1;
}
.screenshots figure.cs-hover figcaption {
  opacity: 1;
}
.screenshots figcaption i {
  font-size: 35px;
}
.screenshots figcaption p {
  margin-bottom: 0;
  text-transform: uppercase;
  font-weight: 400;
}
.screenshots figcaption .caption-content {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -21px;
  margin-left: -100px;
  width: 200px;
  -webkit-transform: translate(0px, 15px);
  -ms-transform: translate(0px, 15px);
  transform: translate(0px, 15px);
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}
.screenshots figure:hover figcaption .caption-content,
.screenshots figure:focus figcaption .caption-content {
  -webkit-transform: translate(0px, 0px);
  -ms-transform: translate(0px, 0px);
  transform: translate(0px, 0px);
}

