/* ==========================================================================
Readmore
========================================================================== */

.read-more.fade-in {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.5s, -webkit-transform 0.5s, line-height 0.2s;
  -moz-transition: opacity 0.5s, -moz-transform 0.5s, line-height 0.2s;
  -ms-transition: opacity 0.5s, -ms-transform 0.5s, line-height 0.2s;
  -o-transition: opacity 0.5s, -o-transform 0.5s, line-height 0.2s;
  transition: opacity 0.5s, transform 0.5s, line-height 0.2s;
  -webkit-transform: translateX(-5em);
  -moz-transform: translateX(-5em);
  -ms-transform: translateX(-5em);
  -o-transform: translateX(-5em);
  transform: translateX(-5em);
}

.read-more.fade-in.animated {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateX(0em);
  -moz-transform: translateX(0em);
  -ms-transform: translateX(0em);
  -o-transform: translateX(0em);
  transform: translateX(0em);
}

a:hover .read-more {
  line-height: 2;
  margin-top: 1px;
  padding-bottom: 3px;
  border: 1px solid #09f;
}

.read-more-state {
  display: none;
}

.read-more-target {
  opacity: 0;
  max-height: 0;
  font-size: 0;
  transition: 0.25s ease;
}

.read-more-state:checked ~ .read-more-wrap .read-more-target {
  opacity: 1;
  font-size: inherit;
  max-height: 999em;
}

.read-more-state ~ .read-more-trigger:before {
  content: "Read more";
}

.read-more-state:checked ~ .read-more-trigger:before {
  content: "Show less";
}

.read-more-trigger {
  cursor: pointer;
  display: inline-block;
  color: #463833;
  font-weight: normal;
  text-decoration: underline;
}

.read-more-trigger:hover {
  text-decoration: none;
}

/* ==========================================================================
Navigation
========================================================================== */
.nav-toggle {
  position: absolute;
  top: 12px;
  right: 15px;
  z-index: 999999;
  padding: 10px 35px 16px 0px;
  cursor: pointer;
}
.nav-toggle:focus {
  outline: none;
}
.nav-toggle span,
.nav-toggle span:before,
.nav-toggle span:after {
  content: "";
  position: absolute;
  display: block;
  width: 35px;
  height: 5px;
  border-radius: 1px;
  background: #fff;
  cursor: pointer;
}

.nav-toggle span:hover {
  background: #463833;
}

.nav-toggle span:before {
  top: -10px;
}
.nav-toggle span:after {
  bottom: -10px;
}
.nav-toggle span,
.nav-toggle span:before,
.nav-toggle span:after {
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}
.nav-toggle.active span {
  background-color: transparent;
}
.nav-toggle.active span:before,
.nav-toggle.active span:after {
  top: 0;
}
.nav-toggle.active span:before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.nav-toggle.active span:after {
  top: 10px;
  -webkit-transform: translateY(-10px) rotate(-45deg);
  -ms-transform: translateY(-10px) rotate(-45deg);
  transform: translateY(-10px) rotate(-45deg);
}
.navicon {
  position: relative;
  height: 26px;
}
.navicon p {
  margin: 1px 50px 0 0;
}
.navicon-fixed {
  position: fixed;
  top: 59px;
  right: 143px;
}

.mobile-menu {
  padding-top: 11px;
  height: 50px;
  background-color: #715b38;
}
