.screenshots figure {
  position: relative;
  overflow: hidden; }

.screenshots figure img {
  width: 100%;
  height: 100%;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out; }

.screenshots figure:hover img,
